"use client"

import { useData } from "@/lib/data-context"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Briefcase, Lightbulb, FileText, Users, ArrowUpRight } from "lucide-react"
import Link from "next/link"

export default function DashboardPage() {
    const { services, insights, caseStudies, jobs } = useData()

    const stats = [
        {
            title: "Total Services",
            value: services.length,
            icon: Briefcase,
            color: "text-[#7B3FEF]",
            href: "/dashboard/services",
        },
        {
            title: "Published Insights",
            value: insights.length,
            icon: Lightbulb,
            color: "text-[#00D9FF]",
            href: "/dashboard/insights",
        },
        {
            title: "Case Studies",
            value: caseStudies.length,
            icon: FileText,
            color: "text-[#7B3FEF]",
            href: "/dashboard/case-studies",
        },
        {
            title: "Open Positions",
            value: jobs.length,
            icon: Users,
            color: "text-[#00D9FF]",
            href: "/dashboard/careers",
        },
    ]

    return (
        <div className="space-y-8">
            <div>
                <h1 className="text-3xl font-bold font-heading tracking-tight">Dashboard Overview</h1>
                <p className="text-muted-foreground">Welcome back to TECHNOVA control center.</p>
            </div>

            <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-4">
                {stats.map((stat) => (
                    <Link key={stat.title} href={stat.href}>
                        <Card className="hover:border-primary/50 transition-colors cursor-pointer h-full">
                            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                                <CardTitle className="text-sm font-medium">
                                    {stat.title}
                                </CardTitle>
                                <stat.icon className={`h-4 w-4 ${stat.color}`} />
                            </CardHeader>
                            <CardContent>
                                <div className="text-2xl font-bold">{stat.value}</div>
                                <p className="text-xs text-muted-foreground flex items-center mt-1">
                                    View details <ArrowUpRight className="h-3 w-3 ml-1" />
                                </p>
                            </CardContent>
                        </Card>
                    </Link>
                ))}
            </div>

            <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-7">
                <Card className="col-span-4">
                    <CardHeader>
                        <CardTitle>Recent Activity</CardTitle>
                    </CardHeader>
                    <CardContent>
                        <div className="space-y-4">
                            <p className="text-sm text-muted-foreground">
                                No recent activity recorded in this session.
                            </p>
                        </div>
                    </CardContent>
                </Card>
                <Card className="col-span-3">
                    <CardHeader>
                        <CardTitle>Quick Actions</CardTitle>
                    </CardHeader>
                    <CardContent className="space-y-2">
                        <Link href="/dashboard/services/new" className="flex items-center justify-between p-2 hover:bg-muted rounded-lg transition-colors text-sm">
                            <span>Add New Service</span>
                            <ArrowUpRight className="h-4 w-4" />
                        </Link>
                        <Link href="/dashboard/insights/new" className="flex items-center justify-between p-2 hover:bg-muted rounded-lg transition-colors text-sm">
                            <span>Publish Insight</span>
                            <ArrowUpRight className="h-4 w-4" />
                        </Link>
                        <Link href="/dashboard/case-studies/new" className="flex items-center justify-between p-2 hover:bg-muted rounded-lg transition-colors text-sm">
                            <span>Add Case Study</span>
                            <ArrowUpRight className="h-4 w-4" />
                        </Link>
                        <Link href="/dashboard/careers/new" className="flex items-center justify-between p-2 hover:bg-muted rounded-lg transition-colors text-sm">
                            <span>Post Job Opening</span>
                            <ArrowUpRight className="h-4 w-4" />
                        </Link>
                    </CardContent>
                </Card>
            </div>
        </div>
    )
}
